@font-face {
  font-family: 'SourceSerifPro-Regular';
  src: url('fonts/Source_Serif_Pro/SourceSerifPro-Regular.ttf');
}

@font-face {
  font-family: 'SourceSerifPro-Bold';
  src: url('fonts/Source_Serif_Pro/SourceSerifPro-Bold.ttf');
}

@font-face {
  font-family: 'Spectral-Regular';
  src: url('fonts/Spectral/Spectral-Regular.ttf');
}

@font-face {
  font-family: 'Spectral-Bold';
  src: url('fonts/Spectral/Spectral-Bold.ttf');
}

@font-face {
  font-family: 'Press_Start_2P';
  src: url('fonts/Press_Start_2P/PressStart2P-Regular.ttf');
}

body {
  background-color: #222;
  font-family: 'SourceSerifPro-Regular';
}

#background {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-image: url("img/bg2.png");
  background-position: center;
}

#scroll-container {
  position: absolute;
  width: 100vw;
  height: 100vh;
  min-height: 100vh;

  display: flex;
  justify-content: center;
  overflow: scroll;
}

.v-stack {
  display: flex;
  flex-direction: column;
}

.h-stack {
  display: flex;
  flex-direction: row;
}

.white-text {
  color: white;
}

#cover-photos-container {
  height: 400px;
  margin-bottom: 44px;
}

#band-header-image {
  object-position: top;
  object-fit: cover;
  width: 100%;
}

#band-live-image {
  border-color: rgb(0, 0, 0);
  border-width: 3px;
  border-style: solid;
  position: absolute;
  left: 32px;
  top: 140px;
  object-fit: cover;
  height: 300px;
  width: 300px;
  border-radius: 200px;
}

#responsive-width-container {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 1024px;
  background-color: #1d1d1de4;
  height: fit-content;
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 8px black;
}

h3 {
  font-family: 'Spectral-Bold';
  font-size: 28px;
  margin-top: 4px;
  margin-bottom: 4px;
}

#main-text-content-contaier {
  padding: 24px;
}

.band-header-image {
  width: 100%;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.3);
}

#title-headline-container {
  align-items: flex-start;
  margin-bottom: 0px;
  min-width: 300px;
}

#title-headline-container h1 {
  margin-top: 8px;
  font-family: 'Press_Start_2P';
}

#title-headline-container h2 {
  margin-top: 4px;
  margin-bottom: 16px;
  font-family: 'Spectral-Bold';
}

.contacts {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.contact {
  font-family: 'Spectral-Regular';
  font-weight: 500;
  font-size: 18px;
  margin-top: 2px;
  margin-bottom: 2px;
}

#bio-container {
  background-color: #111111b5;
  padding: 16px;
  border-radius: 12px;
  margin-bottom: 24px;
}

#bio-container p {
  margin-top: 8px;
  margin-bottom: 8px;
  /* font-family: 'Spectral-Regular'; */
  line-height: 25px;
  font-size: 18px;
}

.divider {
  width: 100%;
  height: 1px;
  background-image: linear-gradient(to right, transparent, rgba(255, 255, 255, 0.355), transparent);
  margin-bottom: 24px;
  margin-top: 24px;
}

.divider.divider-rainbow {
  background-image: linear-gradient(to right, transparent, rgb(140, 0, 255) , rgb(255, 0, 140), rgb(0, 255, 60), rgb(0, 81, 255), rgb(140, 0, 255), transparent);
}

#quotes-content-container {
  background-color: #111111b5;
  padding: 16px;
  padding-left: 8px;
  border-radius: 12px;
  margin-bottom: 24px;
}

.quote-container {
  border-left: rgba(255, 255, 255, 0.363);
  border-left-width: 1;
  border-left-style: solid;
  margin-bottom: 30px;
}

.quote-container .quote {
  margin-left: 24px;
  font-size: 16px;
  font-family: 'SourceSerifPro-Bold';
}

.quote-container .quote-from {
  margin-left: 24px;
  font-family: 'Press_Start_2P';
  font-size: 16px;
  color:rgb(198, 255, 182)
}

#videos-embed-container {
  padding-top: 8px;
  padding-bottom: 8px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 8px;
}

#videos-embed-container iframe {
  padding-top: 8px;
  padding-bottom: 8px;
}

#members-content-container {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.member-container {
  
  margin-top: 12px;
  margin-bottom: 12px;
  background-color: #111111b5;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 40px;
  padding-right: 40px;
  border-radius: 75px;
  border-style: solid;
  border-width: 2px;
  border-color: rgba(255, 255, 255, 0.233);
}

.member-container .member-name {
  font-family: 'SourceSerifPro-Bold';
  font-size: 24px;
  color: white;
}

.member-container .member-role {
  font-family: 'SourceSerifPro-Regular';
  font-size: 22px;
}

.member-container .member-subtitle {
  font-family: 'SourceSerifPro-Bold';
  font-size: 18px;
  margin-top: 8px;
  margin-bottom: 0px;
  color: rgb(203, 203, 203);
}


.social-links {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 16px;
  min-width: 300px;
}

.social-link {
  display: flex;
  align-items: flex-start;
  flex-direction: row;
  align-items: center;
  gap: 8px;
}

a.social-link {
  font-family: 'spectral-bold';
  font-size: 20px;
  color: white;
  text-decoration: none;
}

.social-link .stats {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.8);
}

.social-link .text {
  padding-bottom: 2px;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: rgba(255, 255, 255, 0.2);
}

#header-socials-stack {
  gap: 20px;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;
}

#highlights-container {
  font-size: 19px;
}

#highlights-container a {
  color: rgb(198, 255, 182);
}

@media only screen and (max-width: 600px) {
  #responsive-width-container {
    max-width: 100vw;
  }

  #main-text-content-contaier {
    max-width: 100vw;
    padding: 12px;
  }

  #cover-photos-container {
    max-width: 100vw;
    height: 280px;
    margin-bottom: 24px;
  }

  #band-live-image {
    left: 12px;
    top: 100px;
    height: 200px;
    width: 200px;
  }
}

